<template>
    <div class="table">
      <el-row>
      <el-col :span="5">
        <el-input v-model="input" placeholder="请输入城市名" />
      </el-col>
      <el-col :span="2">
        <el-button type="primary" round @click="search">Search</el-button>
      </el-col>
    </el-row>
    <br />
      <el-row>
      <el-col :span="8">
        <h2>&nbsp;以下是{{ input2 }}历年空气质量指数</h2>
      </el-col>
      <el-col :span="2">
                <b style="font-size: medium;">按月份查询</b>
      </el-col>
      <el-col :span="2">
        <el-date-picker v-model="selectbymonth"  value-format="YYYY-MM" type="month" @change="change"
          placeholder="选择查询日期" />
      </el-col>
      <el-col :span="10"> <a :href="pre">
          <b style="font-size: medium;">{{ input2 }}空气质量预测</b>
        </a></el-col>
    </el-row>
    <el-table :data="
    tableData.slice((currentPage - 1) * pagesize, currentPage * pagesize)" lazy     
    :header-cell-style="{'text-align':'center'}"
    :cell-style="{'text-align':'center'}"
    style="width: 100%"  
    >

      <el-table-column prop="date" label="日期" sortable :sort-orders="['ascending', 'descending']"/>
      <el-table-column prop="grade" label="等级" />
      <el-table-column prop="aqi" label="空气质量" sortable :sort-orders="['ascending', 'descending']" />
      <el-table-column prop="rank" label="排名" sortable :sort-orders="['ascending', 'descending']" />
      <el-table-column prop="pm25" label="PM2.5" />
      <el-table-column prop="pm10" label="PM10" />
      <el-table-column prop="so2" label="So2" />
      <el-table-column prop="no2" label="No2" />
      <el-table-column prop="co" label="CO" />
      <el-table-column prop="o3" label="O3" />
    </el-table> 
  </div>
    <div class="pagination">
      <el-pagination class="el-pagination"  @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage"
      :page-sizes="[10, 20, 30, 40, 50, tableData.length]" :page-size="pagesize" :total="tableData.length"
      layout="total, sizes, prev, pager, next, jumper" ></el-pagination>
    </div>
</template>

<script >
import router from "@/router";

import citys from "../assets/citys.json";
export default {
  name: "aqi",
  data() {
    return {
      selectdate: '',
      currentPage: 1,
      pagesize: 31,
      tableData: "",
      input: "",
      input2: "西安",
      selectbymonth: '',
    };
  },
  created() {
    const city = router.currentRoute.value.params.city;
    this.input2 = citys[city];
    let _this = this;
    const axios = require("axios");
    const paramsObj = { city: city };
    axios
      .get("http://localhost:8001/aqi/lists", { params: paramsObj })
      .then((res) => {
        _this.tableData = res.data;
      })
      .catch((err) => {
        console.error(err);
      });
  },
  computed: {
    pre() {
      return '/aqi/prediction/' + this.input2
    }
  },

  methods: {
    change() {

      let _this = this;
      const axios = require("axios");
      const paramsObj = { city: _this.input2, date: _this.selectbymonth };
      axios
        .get("http://localhost:8001/aqi/selectbydate", { params: paramsObj })
        .then((res) => {
          _this.tableData = res.data;
        })
        .catch((err) => {
          console.error(err);
        });

    },
    search: function () {
      let _this = this;
      const axios = require("axios");
      if (_this.input.charCodeAt(0) <= 255) {
        _this.input = citys[_this.input];
      }
      const paramsObj = { city: _this.input };
      axios
        .get("http://localhost:8001/aqi/lists", { params: paramsObj })
        .then((res) => {
          _this.tableData = res.data;
          _this.input2 = _this.input;
        })
        .catch((err) => {
          console.error(err);
          alert("抱歉暂未收录该城市空气质量指数");
        });
    },
    handleSizeChange: function (val) {
      this.pagesize = val;
    },
    handleCurrentChange: function (currentPage) {
      this.currentPage = currentPage;
    },
  },
};
</script> 
<style>
.table {
  width: auto;
  height: auto;
  margin-left: 100px;
  margin-right: 100px;

}
.pagination{
  width: 100%;
}
h2 {
  float: left;
}

.el-pagination{
  padding-left: 250px;
}
.el-table::before {
	 left: 0;
	 bottom: 0;
	 width: 100%;
	 height: 0px;
}
.el-table, .el-table__expanded-cell{
    background-color: transparent;
    padding-bottom: 15px;
}
/* 表格内背景颜色 */
.el-table th,
.el-table tr,
.el-table td {
  
  background-color: transparent;
}

</style>